// app/dashboard/page.tsx
"use client";

import React, { useEffect, useMemo, useState } from "react";
import Link from "next/link";
import {
  ChartBarIcon,
  ClockIcon,
  DocumentMagnifyingGlassIcon,
  ArrowPathIcon,
  ExclamationTriangleIcon,
  ShieldCheckIcon,
  SparklesIcon,
} from "@heroicons/react/24/outline";

type ComparisonItem = {
  id: number;
  document_name: string;
  version_old_label: string;
  version_new_label: string;
  created_at: string;
  overall_risk_level?: "LOW" | "MEDIUM" | "HIGH" | string | null;
  changes_count?: number;
};

const API_BASE =
  process.env.NEXT_PUBLIC_API_BASE?.trim() || "http://127.0.0.1:8000";

function clamp(n: number, min: number, max: number) {
  return Math.max(min, Math.min(max, n));
}

function formatDate(iso: string) {
  try {
    return new Date(iso).toLocaleString("th-TH", {
      month: "short",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  } catch {
    return iso;
  }
}

function normalizeRisk(r?: string | null) {
  const x = (r || "").toUpperCase();
  if (x.includes("HIGH")) return "HIGH";
  if (x.includes("MEDIUM")) return "MEDIUM";
  if (x.includes("LOW")) return "LOW";
  return "LOW";
}

function RiskPill({ level }: { level?: string | null }) {
  const r = normalizeRisk(level);
  const base =
    "inline-flex items-center rounded-full px-2.5 py-1 text-xs font-semibold border";
  if (r === "HIGH")
    return (
      <span className={`${base} bg-red-50 text-red-700 border-red-200`}>
        HIGH
      </span>
    );
  if (r === "MEDIUM")
    return (
      <span className={`${base} bg-amber-50 text-amber-700 border-amber-200`}>
        MEDIUM
      </span>
    );
  return (
    <span className={`${base} bg-emerald-50 text-emerald-700 border-emerald-200`}>
      LOW
    </span>
  );
}

function StatCard({
  title,
  value,
  sub,
  accent = "blue",
}: {
  title: string;
  value: React.ReactNode;
  sub?: React.ReactNode;
  accent?: "blue" | "violet" | "emerald" | "rose" | "amber";
}) {
  const ring =
    accent === "violet"
      ? "ring-violet-200/60"
      : accent === "emerald"
      ? "ring-emerald-200/60"
      : accent === "rose"
      ? "ring-rose-200/60"
      : accent === "amber"
      ? "ring-amber-200/60"
      : "ring-blue-200/60";

  const glow =
    accent === "violet"
      ? "from-violet-600/15 via-fuchsia-500/10 to-transparent"
      : accent === "emerald"
      ? "from-emerald-600/15 via-sky-500/10 to-transparent"
      : accent === "rose"
      ? "from-rose-600/15 via-amber-500/10 to-transparent"
      : accent === "amber"
      ? "from-amber-600/15 via-fuchsia-500/10 to-transparent"
      : "from-blue-600/15 via-violet-500/10 to-transparent";

  return (
    <div
      className={`relative overflow-hidden rounded-3xl border border-white/50 bg-white/60 backdrop-blur-xl shadow-[0_20px_60px_-45px_rgba(2,6,23,0.35)] ring-1 ${ring}`}
    >
      <div className={`pointer-events-none absolute inset-0 bg-gradient-to-br ${glow}`} />
      <div className="relative p-5">
        <p className="text-xs font-semibold uppercase tracking-wide text-slate-600">
          {title}
        </p>
        <div className="mt-2 text-3xl font-extrabold text-slate-900">
          {value}
        </div>
        {sub && <div className="mt-2 text-sm text-slate-600">{sub}</div>}
      </div>
    </div>
  );
}

function MiniLineChart({
  points,
  height = 120,
}: {
  points: number[];
  height?: number;
}) {
  const w = 520;
  const h = height;
  const pad = 12;

  const max = Math.max(1, ...points);
  const min = 0;

  const toX = (i: number) =>
    pad + (i * (w - pad * 2)) / Math.max(1, points.length - 1);
  const toY = (v: number) =>
    h - pad - ((v - min) * (h - pad * 2)) / Math.max(1, max - min);

  const d = points
    .map((v, i) => `${i === 0 ? "M" : "L"} ${toX(i)} ${toY(v)}`)
    .join(" ");

  const area = `${d} L ${toX(points.length - 1)} ${h - pad} L ${toX(0)} ${
    h - pad
  } Z`;

  return (
    <svg
      viewBox={`0 0 ${w} ${h}`}
      className="w-full h-[140px]"
      aria-label="trend chart"
      role="img"
    >
      {/* grid */}
      {[0, 1, 2, 3].map((k) => {
        const y = pad + (k * (h - pad * 2)) / 3;
        return (
          <line
            key={k}
            x1={pad}
            y1={y}
            x2={w - pad}
            y2={y}
            stroke="rgba(15,23,42,0.10)"
            strokeWidth="1"
          />
        );
      })}

      {/* area */}
      <path d={area} fill="rgba(99,102,241,0.12)" />

      {/* line */}
      <path d={d} fill="none" stroke="rgba(99,102,241,0.9)" strokeWidth="3" />

      {/* points */}
      {points.map((v, i) => (
        <circle
          key={i}
          cx={toX(i)}
          cy={toY(v)}
          r="4"
          fill="white"
          stroke="rgba(99,102,241,0.9)"
          strokeWidth="2"
        />
      ))}
    </svg>
  );
}

function Donut({
  high,
  medium,
  low,
}: {
  high: number;
  medium: number;
  low: number;
}) {
  const total = Math.max(1, high + medium + low);
  const r = 54;
  const c = 2 * Math.PI * r;

  const pHigh = (high / total) * c;
  const pMed = (medium / total) * c;
  const pLow = (low / total) * c;

  const dashHigh = `${pHigh} ${c - pHigh}`;
  const dashMed = `${pMed} ${c - pMed}`;
  const dashLow = `${pLow} ${c - pLow}`;

  // offsets (stack segments)
  const offHigh = 0;
  const offMed = -pHigh;
  const offLow = -(pHigh + pMed);

  return (
    <div className="flex items-center gap-5">
      <svg viewBox="0 0 140 140" className="h-[140px] w-[140px]" role="img">
        <circle cx="70" cy="70" r={r} fill="none" stroke="rgba(15,23,42,0.08)" strokeWidth="14" />
        <circle
          cx="70"
          cy="70"
          r={r}
          fill="none"
          stroke="rgba(239,68,68,0.85)"
          strokeWidth="14"
          strokeDasharray={dashHigh}
          strokeDashoffset={offHigh}
          strokeLinecap="round"
          transform="rotate(-90 70 70)"
        />
        <circle
          cx="70"
          cy="70"
          r={r}
          fill="none"
          stroke="rgba(245,158,11,0.85)"
          strokeWidth="14"
          strokeDasharray={dashMed}
          strokeDashoffset={offMed}
          strokeLinecap="round"
          transform="rotate(-90 70 70)"
        />
        <circle
          cx="70"
          cy="70"
          r={r}
          fill="none"
          stroke="rgba(16,185,129,0.85)"
          strokeWidth="14"
          strokeDasharray={dashLow}
          strokeDashoffset={offLow}
          strokeLinecap="round"
          transform="rotate(-90 70 70)"
        />
        <text
          x="70"
          y="67"
          textAnchor="middle"
          className="fill-slate-900"
          style={{ fontSize: 18, fontWeight: 800 }}
        >
          {high + medium + low}
        </text>
        <text
          x="70"
          y="88"
          textAnchor="middle"
          className="fill-slate-600"
          style={{ fontSize: 12, fontWeight: 700 }}
        >
          comparisons
        </text>
      </svg>

      <div className="space-y-2 text-sm">
        <div className="flex items-center justify-between gap-6">
          <div className="flex items-center gap-2">
            <span className="h-2.5 w-2.5 rounded-full bg-red-500" />
            <span className="font-semibold text-slate-900">HIGH</span>
          </div>
          <span className="font-bold text-slate-900">{high}</span>
        </div>
        <div className="flex items-center justify-between gap-6">
          <div className="flex items-center gap-2">
            <span className="h-2.5 w-2.5 rounded-full bg-amber-500" />
            <span className="font-semibold text-slate-900">MEDIUM</span>
          </div>
          <span className="font-bold text-slate-900">{medium}</span>
        </div>
        <div className="flex items-center justify-between gap-6">
          <div className="flex items-center gap-2">
            <span className="h-2.5 w-2.5 rounded-full bg-emerald-500" />
            <span className="font-semibold text-slate-900">LOW</span>
          </div>
          <span className="font-bold text-slate-900">{low}</span>
        </div>
      </div>
    </div>
  );
}

export default function DashboardPage() {
  const [items, setItems] = useState<ComparisonItem[]>([]);
  const [loading, setLoading] = useState(false);
  const [pinging, setPinging] = useState(false);
  const [apiOk, setApiOk] = useState<boolean | null>(null);
  const [error, setError] = useState<string | null>(null);

  const fetchComparisons = async () => {
    setLoading(true);
    setError(null);

    try {
      const res = await fetch(`${API_BASE}/comparisons?limit=250`, {
        cache: "no-store",
      });
      if (!res.ok) throw new Error(`โหลด comparisons ไม่สำเร็จ (${res.status})`);
      const data: ComparisonItem[] = await res.json();
      setItems(Array.isArray(data) ? data : []);
      setApiOk(true);
    } catch (e: any) {
      setError(e?.message || "เกิดข้อผิดพลาดในการโหลดข้อมูล");
      setApiOk(false);
      setItems([]);
    } finally {
      setLoading(false);
    }
  };

  const pingApi = async () => {
    setPinging(true);
    try {
      // ใช้ endpoint เดิมที่มีแน่ ๆ (comparisons) เป็น health check
      const res = await fetch(`${API_BASE}/comparisons?limit=1`, {
        cache: "no-store",
      });
      setApiOk(res.ok);
    } catch {
      setApiOk(false);
    } finally {
      setPinging(false);
    }
  };

  useEffect(() => {
    fetchComparisons();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const computed = useMemo(() => {
    const list = [...items].sort(
      (a, b) => new Date(b.created_at).getTime() - new Date(a.created_at).getTime()
    );

    const comparisons = list.length;

    const docsSet = new Set(list.map((x) => x.document_name).filter(Boolean));
    const documents = docsSet.size;

    // ประมาณจำนวน versions จาก labels ที่เจอใน comparisons
    const vSet = new Set<string>();
    list.forEach((x) => {
      if (x.version_old_label) vSet.add(`${x.document_name}::${x.version_old_label}`);
      if (x.version_new_label) vSet.add(`${x.document_name}::${x.version_new_label}`);
    });
    const versions = vSet.size;

    let high = 0,
      med = 0,
      low = 0;

    list.forEach((x) => {
      const r = normalizeRisk(x.overall_risk_level);
      if (r === "HIGH") high++;
      else if (r === "MEDIUM") med++;
      else low++;
    });

    // trend last 14 days
    const days = 14;
    const now = new Date();
    const buckets = new Array(days).fill(0);
    const labels = new Array(days).fill("").map((_, i) => {
      const d = new Date(now);
      d.setDate(now.getDate() - (days - 1 - i));
      return d.toLocaleDateString("th-TH", { month: "short", day: "numeric" });
    });

    list.forEach((x) => {
      const d = new Date(x.created_at);
      const diff = Math.floor((now.getTime() - d.getTime()) / (1000 * 60 * 60 * 24));
      if (diff >= 0 && diff < days) {
        const idx = days - 1 - diff;
        buckets[idx] += 1;
      }
    });

    // Top documents by changes_count
    const byDoc = new Map<string, { doc: string; totalChanges: number; count: number }>();
    list.forEach((x) => {
      const doc = x.document_name || "Untitled";
      const cur = byDoc.get(doc) || { doc, totalChanges: 0, count: 0 };
      cur.totalChanges += x.changes_count || 0;
      cur.count += 1;
      byDoc.set(doc, cur);
    });

    const topDocs = [...byDoc.values()]
      .sort((a, b) => b.totalChanges - a.totalChanges)
      .slice(0, 5);

    const latest = list.slice(0, 8);

    const lastRun = latest[0]?.created_at ? formatDate(latest[0].created_at) : "—";

    return {
      comparisons,
      documents,
      versions,
      high,
      med,
      low,
      trend: buckets,
      trendLabels: labels,
      latest,
      topDocs,
      lastRun,
    };
  }, [items]);

  return (
    <div className="space-y-6">
      {/* Header hero */}
      <div className="relative overflow-hidden rounded-3xl border border-white/50 bg-white/60 backdrop-blur-xl shadow-[0_20px_60px_-45px_rgba(2,6,23,0.35)]">
        <div className="pointer-events-none absolute -right-20 -top-24 h-64 w-64 rounded-full bg-fuchsia-300/20 blur-3xl" />
        <div className="pointer-events-none absolute -left-24 -bottom-32 h-72 w-72 rounded-full bg-violet-300/20 blur-3xl" />

        <div className="p-6 md:p-7 flex flex-col md:flex-row md:items-center md:justify-between gap-4">
          <div>
            <div className="inline-flex items-center gap-2 rounded-full border border-slate-200 bg-white/70 px-3 py-1 text-xs font-semibold text-slate-700">
              <SparklesIcon className="h-4 w-4 text-violet-600" />
              AI Document Versioning Compare
            </div>
            <h1 className="mt-3 text-2xl md:text-3xl font-extrabold text-slate-900">
              Dashboard
            </h1>
            <p className="mt-1 text-sm text-slate-600">
              ภาพรวมเอกสาร เวอร์ชัน ความเสี่ยง และการเปรียบเทียบล่าสุด (Realtime จาก Backend)
            </p>
          </div>

          <div className="flex flex-wrap items-center gap-2">
            <button
              onClick={pingApi}
              disabled={pinging}
              className="inline-flex items-center gap-2 rounded-full border border-slate-200 bg-white/70 px-4 py-2 text-sm font-semibold text-slate-900 hover:bg-white"
              title="ตรวจสอบว่า API ตอบสนองอยู่ไหม"
            >
              <ShieldCheckIcon className="h-5 w-5 text-emerald-600" />
              {apiOk === null ? "API: ไม่ทราบ" : apiOk ? "API: Online" : "API: Offline"}
              {pinging && <ArrowPathIcon className="h-4 w-4 animate-spin" />}
            </button>

            <button
              onClick={fetchComparisons}
              disabled={loading}
              className="inline-flex items-center gap-2 rounded-full bg-slate-900 px-4 py-2 text-sm font-semibold text-white hover:opacity-95 disabled:opacity-60"
            >
              <ArrowPathIcon className={`h-4 w-4 ${loading ? "animate-spin" : ""}`} />
              รีเฟรชข้อมูล
            </button>

            <Link
              href="/"
              className="inline-flex items-center gap-2 rounded-full bg-gradient-to-r from-violet-600 via-fuchsia-500 to-sky-500 px-4 py-2 text-sm font-semibold text-white hover:brightness-105"
            >
              <DocumentMagnifyingGlassIcon className="h-5 w-5" />
              เปรียบเทียบใหม่
            </Link>

            <Link
              href="/history"
              className="inline-flex items-center gap-2 rounded-full border border-slate-200 bg-white/70 px-4 py-2 text-sm font-semibold text-slate-900 hover:bg-white"
            >
              <ClockIcon className="h-5 w-5 text-slate-700" />
              ไปหน้าประวัติ
            </Link>
          </div>
        </div>

        <div className="px-6 md:px-7 pb-6 text-xs text-slate-500 flex items-center gap-2">
          <span className="inline-flex h-5 w-5 items-center justify-center rounded-full bg-slate-900 text-white text-[10px] font-bold">
            API
          </span>
          <span className="font-semibold text-slate-700">{API_BASE}</span>
          <span>•</span>
          <span>ล่าสุด: {computed.lastRun}</span>
        </div>
      </div>

      {/* Error */}
      {error && (
        <div className="rounded-2xl border border-red-200 bg-red-50 px-4 py-3 text-sm text-red-800 font-semibold flex items-start gap-2">
          <ExclamationTriangleIcon className="h-5 w-5 mt-0.5" />
          <div>
            <div>โหลดข้อมูลไม่สำเร็จ</div>
            <div className="text-red-700/80 font-medium mt-1">{error}</div>
          </div>
        </div>
      )}

      {/* Stats */}
      <div className="grid gap-4 md:grid-cols-3">
        <StatCard
          title="Documents"
          value={computed.documents.toLocaleString()}
          sub={<span>จำนวนชื่อเอกสารที่พบจาก comparisons</span>}
          accent="violet"
        />
        <StatCard
          title="Versions (estimated)"
          value={computed.versions.toLocaleString()}
          sub={<span>คำนวณจาก version labels ที่เจอ</span>}
          accent="blue"
        />
        <StatCard
          title="Comparisons"
          value={computed.comparisons.toLocaleString()}
          sub={<span>จำนวนรายการเปรียบเทียบทั้งหมด</span>}
          accent="emerald"
        />
      </div>

      {/* Charts row */}
      <div className="grid gap-4 lg:grid-cols-3">
        {/* Trend */}
        <div className="lg:col-span-2 rounded-3xl border border-white/50 bg-white/60 backdrop-blur-xl shadow-[0_20px_60px_-45px_rgba(2,6,23,0.35)] p-5">
          <div className="flex items-center justify-between gap-3">
            <div className="flex items-center gap-2">
              <ChartBarIcon className="h-5 w-5 text-violet-700" />
              <h2 className="text-sm font-extrabold text-slate-900">
                แนวโน้มการเปรียบเทียบ (14 วันล่าสุด)
              </h2>
            </div>
            <div className="text-xs text-slate-600 font-semibold">
              รวม: {computed.trend.reduce((a, b) => a + b, 0)}
            </div>
          </div>

          <div className="mt-3 rounded-2xl border border-slate-200 bg-white/70 p-3">
            {computed.comparisons === 0 ? (
              <div className="py-10 text-center text-sm text-slate-600 font-semibold">
                ยังไม่มีข้อมูล comparisons
              </div>
            ) : (
              <>
                <MiniLineChart points={computed.trend} />
                <div className="mt-2 grid grid-cols-7 gap-2 text-[11px] text-slate-500 font-semibold">
                  {computed.trendLabels.slice(-7).map((x, idx) => (
                    <div key={idx} className="text-center">
                      {x}
                    </div>
                  ))}
                </div>
              </>
            )}
          </div>

          <div className="mt-3 text-xs text-slate-600">
            <span className="font-semibold text-slate-900">Tip:</span> ถ้าต้องการกราฟรายเดือน/รายปี
            ผมทำให้ได้แบบ switch ได้ครับ
          </div>
        </div>

        {/* Risk donut */}
        <div className="rounded-3xl border border-white/50 bg-white/60 backdrop-blur-xl shadow-[0_20px_60px_-45px_rgba(2,6,23,0.35)] p-5">
          <div className="flex items-center justify-between gap-3">
            <h2 className="text-sm font-extrabold text-slate-900">
              Distribution: Risk Level
            </h2>
            <span className="text-xs text-slate-600 font-semibold">
              ล่าสุด {computed.lastRun}
            </span>
          </div>

          <div className="mt-3 rounded-2xl border border-slate-200 bg-white/70 p-4">
            <Donut high={computed.high} medium={computed.med} low={computed.low} />
          </div>

          <div className="mt-3 text-xs text-slate-600 font-semibold">
            HIGH ควรรีบตรวจโดยฝ่ายกฎหมาย/ผู้อนุมัติ
          </div>
        </div>
      </div>

      {/* Tables row */}
      <div className="grid gap-4 lg:grid-cols-3">
        {/* Latest comparisons */}
        <div className="lg:col-span-2 rounded-3xl border border-white/50 bg-white/60 backdrop-blur-xl shadow-[0_20px_60px_-45px_rgba(2,6,23,0.35)] overflow-hidden">
          <div className="p-5 flex items-center justify-between gap-3">
            <div>
              <h2 className="text-sm font-extrabold text-slate-900">
                Comparisons ล่าสุด
              </h2>
              <p className="text-xs text-slate-600 font-semibold mt-1">
                คลิกเพื่อดูรายละเอียด diff และถาม AI ต่อได้
              </p>
            </div>
            <Link
              href="/history"
              className="text-xs font-extrabold text-violet-700 hover:underline underline-offset-4"
            >
              ดูทั้งหมด →
            </Link>
          </div>

          <div className="border-t border-slate-200/60">
            {loading ? (
              <div className="p-8 text-center">
                <ArrowPathIcon className="h-8 w-8 animate-spin mx-auto text-violet-600" />
                <p className="mt-3 text-sm text-slate-600 font-semibold">
                  กำลังโหลด...
                </p>
              </div>
            ) : computed.latest.length === 0 ? (
              <div className="p-8 text-center text-sm text-slate-600 font-semibold">
                ยังไม่มีข้อมูล
              </div>
            ) : (
              <div className="divide-y divide-slate-200/60">
                {computed.latest.map((x) => (
                  <div
                    key={x.id}
                    className="p-4 flex flex-col md:flex-row md:items-center gap-3 md:gap-4 hover:bg-white/60 transition"
                  >
                    <div className="flex-1 min-w-0">
                      <div className="flex items-center gap-2">
                        <div className="font-extrabold text-slate-900 truncate">
                          {x.document_name}
                        </div>
                        <RiskPill level={x.overall_risk_level} />
                      </div>
                      <div className="mt-1 text-xs text-slate-600 font-semibold flex flex-wrap items-center gap-2">
                        <span className="rounded-full bg-violet-50 border border-violet-200 px-2 py-0.5 text-violet-700">
                          {x.version_old_label}
                        </span>
                        <span className="text-slate-400">→</span>
                        <span className="rounded-full bg-fuchsia-50 border border-fuchsia-200 px-2 py-0.5 text-fuchsia-700">
                          {x.version_new_label}
                        </span>
                        <span className="text-slate-400">•</span>
                        <span>{formatDate(x.created_at)}</span>
                        {typeof x.changes_count === "number" && (
                          <>
                            <span className="text-slate-400">•</span>
                            <span className="text-slate-900 font-extrabold">
                              {x.changes_count.toLocaleString()}
                            </span>
                            <span>changes</span>
                          </>
                        )}
                      </div>
                    </div>

                    <div className="flex items-center gap-2">
                      <Link
                        href={`/compare/${x.id}`}
                        className="inline-flex items-center justify-center rounded-xl bg-slate-900 text-white px-4 py-2 text-sm font-extrabold hover:opacity-95"
                      >
                        เปิดรายละเอียด
                      </Link>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>

        {/* Top documents */}
        <div className="rounded-3xl border border-white/50 bg-white/60 backdrop-blur-xl shadow-[0_20px_60px_-45px_rgba(2,6,23,0.35)] overflow-hidden">
          <div className="p-5">
            <h2 className="text-sm font-extrabold text-slate-900">
              Top documents (by changes)
            </h2>
            <p className="text-xs text-slate-600 font-semibold mt-1">
              เอกสารที่เปลี่ยนเยอะที่สุดจากข้อมูล comparisons
            </p>
          </div>

          <div className="border-t border-slate-200/60">
            {computed.topDocs.length === 0 ? (
              <div className="p-8 text-center text-sm text-slate-600 font-semibold">
                ยังไม่มีข้อมูล
              </div>
            ) : (
              <div className="divide-y divide-slate-200/60">
                {computed.topDocs.map((d) => {
                  const max = Math.max(1, ...computed.topDocs.map((x) => x.totalChanges));
                  const pct = clamp((d.totalChanges / max) * 100, 6, 100);

                  return (
                    <div key={d.doc} className="p-4">
                      <div className="flex items-start justify-between gap-3">
                        <div className="min-w-0">
                          <div className="font-extrabold text-slate-900 truncate">
                            {d.doc}
                          </div>
                          <div className="mt-1 text-xs text-slate-600 font-semibold">
                            {d.count} comparisons • {d.totalChanges.toLocaleString()} changes
                          </div>
                        </div>
                        <div className="text-xs font-extrabold text-slate-900">
                          {d.totalChanges.toLocaleString()}
                        </div>
                      </div>

                      <div className="mt-3 h-2 rounded-full bg-slate-200/70 overflow-hidden">
                        <div
                          className="h-full rounded-full bg-gradient-to-r from-violet-600 via-fuchsia-500 to-sky-500"
                          style={{ width: `${pct}%` }}
                        />
                      </div>
                    </div>
                  );
                })}
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Footer hint */}
      <div className="text-xs text-slate-600 font-semibold">
        ถ้าคุณอยากให้ Dashboard “สมจริงขึ้น” อีก: ผมเพิ่มได้เลย เช่น
        “Most risky clauses”, “AI action items”, “Download monthly report”, “Role-based widgets”
      </div>
    </div>
  );
}
